import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { Ng2TableModule } from 'ng2-table/ng2-table';
import { NgxDatatableModule } from '@swimlane/ngx-datatable';
import { SharedModule } from '../../shared/shared.module';
import { NgxPaginationModule } from 'ngx-pagination';
import { CommonPopUpModule } from '../commonPopUp/commonPopUp.module';
import { Ng2Rut } from 'ng2-rut';
import { NgxCurrencyModule } from 'ngx-currency';

import { ChequesComponent } from '../cajaCobranza/cheques/cheques.component';
import { ClienteCajaCobranzaPopUpComponent } from '../commonPopUp/clienteCajaCobranzaPopUp/clienteCajaCobranzaPopUp.component';
import { ProtestosComponent } from './protestos/protestos.component';
import { DocumentosClientePopUpComponent } from '../commonPopUp/documentosClientePopUp/documentosClientePopUp.component';
import { BuscaClienteCtaCteComponent } from './buscaClienteCtaCte/buscaClienteCtaCte.component';
import { DetalleClienteCtaCteComponent } from './detalleClienteCtaCte/detalleClienteCtaCte.component';
import { TotalesComponent } from './detalleClienteCtaCte/navDetalleClienteCtaCte/totales/totales.component';
import { PendientesComponent } from './detalleClienteCtaCte/navDetalleClienteCtaCte/pendientes/pendientes.component';
import { PeriodoComponent } from './detalleClienteCtaCte/navDetalleClienteCtaCte/periodo/periodo.component';
import { BuscaTransportistaCtaCteComponent } from './buscaTransportistaCtaCte/buscaTransportistaCtaCte.component';
import { DetalleTransportistaCtaCteComponent } from './detalleTransportistaCtaCte/detalleTransportistaCtaCte.component';
import { TotalesTransportistaComponent } from './detalleTransportistaCtaCte/navDetalleTransportistaCtaCte/totalesTransportista/totalesTransportista.component';
import { PendientesTransportistaComponent } from './detalleTransportistaCtaCte/navDetalleTransportistaCtaCte/pendientesTransportista/pendientesTransportista.component';
import { PeriodoTransportistaComponent } from './detalleTransportistaCtaCte/navDetalleTransportistaCtaCte/periodoTransportista/periodoTransportista.component';
import { PagosComponent } from './pagos/pagos.component';
import { PagoTransportistaComponent } from './pagoTransportista/pagoTransportista.component';
import { TransportistaPagoPopUpComponent } from '../commonPopUp/transportistaPagoPopUp/transportistaPagoPopUp.component';
import { CierreCajaComponent } from './cierraCaja/cierreCaja.component';
import { InformesCajaComponent } from './informesCaja/informesCaja.component';
import { PartidasAbiertasComponent } from './partidasAbiertas/partidasAbiertas.component';
import { PartidasAbiertasTransportistaComponent } from './partidasAbiertasTransportista/partidasAbiertasTransportista.component';
import { ResumenCuentasCorrientesComponent } from './resumenCuentasCorrientes/resumenCuentasCorrientes.component';
import { BuscarReversaPagoComponent } from './buscarReversaPago/buscarReversaPago.component';
import { AgregarReversaPagoComponent } from './agregarReversaPago/agregarReversaPago.component';
import { BuscarArticuloPopUpComponent } from '../commonPopUp/buscarArticuloPopUp/buscarArticuloPopUp.component';
import { DocumentosAsociadosPopUpComponent } from '../commonPopUp/documentosAsociadosPopUp/documentosAsociadosPopUp.component';
import { ResumenDocumentosCobranzaComponent } from './resumenDocumentosCobranza/resumenDocumentosCobranza.component';
import { ResumenProtestosPagoComponent } from './resumenProtestos/resumenProtestos.component';
import { DocumentosDepositoComponent } from './documentosDeposito/documentosDeposito.component';
import { DocumentosCarteraComponent } from './documentosCartera/documentosCartera.component';
import { DetalleDocumentosCobranzaComponent } from './detalleDocumentosCobranza/detalleDocumentosCobranza.component';

const routes: Routes = [
  { path: 'cheques', component: ChequesComponent },
  { path: 'protestos', component: ProtestosComponent },
  { path: 'cuentaCorriente', component: BuscaClienteCtaCteComponent },
  { path: 'detalleCliente', component: DetalleClienteCtaCteComponent },
  { path: 'cuentaCorrienteTransportista', component: BuscaTransportistaCtaCteComponent },
  { path: 'detalleTransportista', component: DetalleTransportistaCtaCteComponent },
  { path: 'pagos', component: PagosComponent },
  { path: 'pagosTransportista', component: PagoTransportistaComponent },
  { path: 'cierreCaja', component: CierreCajaComponent },
  { path: 'informesCaja', component: InformesCajaComponent },
  { path: 'partidasAbiertas', component: PartidasAbiertasComponent },
  { path: 'partidasAbiertasTransportista', component: PartidasAbiertasTransportistaComponent },
  { path: 'resumenCuentasCorrientes', component: ResumenCuentasCorrientesComponent },
  { path: 'buscarReversaPago', component: BuscarReversaPagoComponent },
  { path: 'agregarReversaPago', component: AgregarReversaPagoComponent },
  { path: 'resumenDocumentosCobranza', component: ResumenDocumentosCobranzaComponent },
  { path: 'resumenProtestos', component: ResumenProtestosPagoComponent },
  { path: 'documentosDeposito', component: DocumentosDepositoComponent },
  { path: 'documentosCartera', component: DocumentosCarteraComponent },
  { path: 'detalleDocumentosCobranza', component: DetalleDocumentosCobranzaComponent },
];

@NgModule({
  imports: [
    SharedModule,
    RouterModule.forChild(routes),
    Ng2TableModule,
    NgxDatatableModule,
    NgxPaginationModule,
    CommonPopUpModule,
    Ng2Rut,
    NgxCurrencyModule
  ],
  declarations: [
    ChequesComponent,
    ProtestosComponent,
    BuscaClienteCtaCteComponent,
    DetalleClienteCtaCteComponent,
    TotalesComponent,
    PendientesComponent,
    PeriodoComponent,
    BuscaTransportistaCtaCteComponent,
    DetalleTransportistaCtaCteComponent,
    TotalesTransportistaComponent,
    PendientesTransportistaComponent,
    PeriodoTransportistaComponent,
    PagosComponent,
    PagoTransportistaComponent,
    CierreCajaComponent,
    InformesCajaComponent,
    PartidasAbiertasComponent,
    PartidasAbiertasTransportistaComponent,
    ResumenCuentasCorrientesComponent,
    BuscarReversaPagoComponent,
    AgregarReversaPagoComponent,
    ResumenDocumentosCobranzaComponent,
    ResumenProtestosPagoComponent,
    DocumentosDepositoComponent,
    DocumentosCarteraComponent,
    DetalleDocumentosCobranzaComponent
  ],
  entryComponents: [
    ClienteCajaCobranzaPopUpComponent,
    DocumentosClientePopUpComponent,
    TransportistaPagoPopUpComponent,
    BuscarArticuloPopUpComponent,
    DocumentosAsociadosPopUpComponent]
})
export class CajaCobranzaModule { }
