import { Component, OnInit, OnDestroy } from '@angular/core';
import { FormGroup, Validators, FormControl } from '@angular/forms';
import { CajaCobranzaService } from '../../../core/consultaService/cajaCobranza.service';
import { UtilService } from './../../../services/util.service';
import { BsModalService, BsModalRef } from 'ngx-bootstrap/modal';
import { RutAsociadoPopUpComponent } from '../../commonPopUp/rutAsociadoPopUp/rutAsociadoPopUp.component';
import { NgxSpinnerService } from 'ngx-spinner';
import { AuthService } from '../../../core/consultaService/auth.service';
import { DatePipe } from '@angular/common';
import { ConsultasService } from '../../../core/consultaService/consultas.service';
import { ClientesService } from '../../../core/consultaService/clientes.service';
import { Router } from '@angular/router';
import { ExcelService } from '../../../core/excel/excel.service';
import { DecimalPipe } from '@angular/common';

import { registerLocaleData, Time } from '@angular/common';
import es from '@angular/common/locales/es';

const swal = require('sweetalert');

@Component({
    selector: 'app-resumen-protestos',
    templateUrl: './resumenProtestos.component.html',
    styleUrls: ['./resumenProtestos.component.scss'],
    providers: [DatePipe]
})
export class ResumenProtestosPagoComponent implements OnInit, OnDestroy {

    forma: FormGroup;
    datos: Array<any> = [];
    origen: string;
    clienteModal: any;
    user: any = {};
    subDataCliente: any;
    fechaActual = new Date();
    min: string;
    max: string;
    estados: Array<any>;

    constructor(
        private modalService: BsModalService,
        private cajaCobranzaService: CajaCobranzaService,
        private utilService: UtilService,
        private authService: AuthService,
        public cargador: NgxSpinnerService,
        private setearDatos: ConsultasService,
        private clientesService: ClientesService,
        private router: Router,
        private decimalPipe: DecimalPipe,
        private excelService: ExcelService,
    ) {
        this.forma = new FormGroup({
            rutCliente: new FormControl('', Validators.required),
            nombreCliente: new FormControl({ value: '', disabled: true }),
            fechaDesde: new FormControl('', Validators.required),
            fechaHasta: new FormControl('', Validators.required),
            estado: new FormControl(''),
        });
    }

    ngOnInit() {
        this.user = this.authService.getDatosUser();
        this.subDataCliente = this.setearDatos.datos$.subscribe(resData => {
            this.setData(resData);
        });
        this.forma.controls.fechaDesde.setValue(this.utilService.dateToStringFormat(this.fechaActual));
        this.forma.controls.fechaHasta.setValue(this.utilService.dateToStringFormat(this.fechaActual));
        this.min = '1900-01-01';
        this.max = this.utilService.dateToStringFormat(this.fechaActual);
        this.getEstados();

        registerLocaleData( es );

    }

    ngOnDestroy() {
        if (this.subDataCliente !== undefined) {
            this.subDataCliente.unsubscribe();
        }
    }

    openModalCliente() {
        this.origen = 'clientePedido';
        const initialState = {
            titulo: 'Cliente',
            origen: 'clientePedido'
        };
        this.clienteModal = this.modalService.show(RutAsociadoPopUpComponent, { initialState, backdrop: 'static', keyboard: false });
    }

    setData(resData) {
        if (resData) {
            switch (this.origen) {
                case 'clientePedido': {
                    this.forma.controls.rutCliente.setValue(resData.cli_nrutcliente + '' + resData.cli_sdigverificador);
                    this.forma.controls.nombreCliente.setValue(resData.cli_snombre);
                    break;
                }
            }
        }
    }

    getEstados() {
        const request = {
            parcodparametro01: '',
            parcodparametro02: '',
            parcodparametro03: '',
            parcodparametro04: '',
            parcodparametro05: '',
            parnumparametro: 45
        };

        this.setearDatos.getParametros(request)
            .subscribe((data: any) => {
                if (data) {
                    if (data.general.length === 0 && data.error.codError === 0) {
                        swal('Atención', 'No se encontraron resultados', 'info');
                    } else {
                        this.estados = data.general;
                        this.estados.unshift({
                            par_codparametro01: '',
                            par_deslargo01: '(Todos)'
                        });
                    }
                }
            },
                error => {
                });
    }

    buscar() {

        if (this.validarForm()) {
            return;
        }

        this.cargador.show();
        const request = {
            nRutOperador: Number(this.user.nrutoperador),
            dFhoProtesto1: (this.forma.controls.fechaDesde.value).replace(/-/g, ''),
            dFhoProtesto2: (this.forma.controls.fechaHasta.value).replace(/-/g, ''),
            sCodEstado: this.forma.controls.estado.value === '' ? null : this.forma.controls.estado.value,
            nRutCliente: this.forma.controls.rutCliente.value === '' ? 0 : Number(this.forma.controls.rutCliente.value.slice(0, -1)),
        };

        this.cajaCobranzaService.buscaRptBuscaProtestos(request)
            .subscribe((data: any) => {
                this.cargador.hide();
                if (data) {
                    if (data.error.codError !== 0) {
                        swal('Atención', 'Ha ocurrido un error inesperado!', 'error');
                        return;
                    }
                    if (data.protesto.length === 0 && data.error.codError === 0) {
                        this.construirReporte(data);
                        //swal('Atención', 'No se encontraron resumen protestos', 'info');
                    } else {
                        //console.log(data);
                        // Generar Excel
                        this.construirReporte(data);
                    }
                } else {
                    swal('Atención', 'Ha ocurrido un error inesperado!', 'error');
                }
            },
                error => {
                    this.cargador.hide();
                    swal('Atención', 'Ha ocurrido un error inesperado!', 'error');
                });
    }

    validarForm() {

        if (this.forma.controls.fechaDesde.value !== '' && this.forma.controls.fechaHasta.value !== '') {
            if (new Date(this.forma.controls.fechaDesde.value) > new Date(this.forma.controls.fechaHasta.value)) {
                swal('Atención', 'Fecha Desde debe ser menor a la Fecha Hasta!', 'error');
                return true;
            }
            if (new Date(this.forma.controls.fechaHasta.value) < new Date(this.forma.controls.fechaDesde.value)) {
                swal('Atención', 'Fecha Hasta debe ser mayor a la Fecha Desde!', 'error');
                return true;
            }
        }


        return false;

    }

    fechaDesdeChanged(fecha) {
        this.min = fecha;
    }

    fechaHastaChanged(fecha) {
        this.max = fecha;
    }

    validaRut() {

        if (this.forma.controls.rutCliente.value === '') {
            return;
        }

        if (!this.utilService.isValidRUT(this.forma.controls.rutCliente.value)) {
            this.forma.controls.rutCliente.setValue('');
            this.forma.controls.nombreCliente.setValue('');
            swal('Atención', 'El RUT es incorrecto', 'error');
            return true;
        } else {
            this.cargador.show();

            let rutCliente = this.forma.controls.rutCliente.value;

            rutCliente = rutCliente.substring(0, rutCliente.length - 1);

            const request = {
                rutClientes: Number(rutCliente),
                rutOperador: Number(this.user.nrutoperador),
            };

            this.clientesService.getDetallesClientes(request)
                .subscribe((data: any) => {
                    if (data) {
                        if (data.error.codError !== 0) {
                            this.forma.controls.rutCliente.setValue('');
                            this.forma.controls.nombreCliente.setValue('');
                            swal('Atención', 'Ha ocurrido un error inesperado!', 'error');
                            return;
                        }
                        if (data.cliente.length === 0 && data.error.codError === 0) {
                            this.forma.controls.rutCliente.setValue('');
                            this.forma.controls.nombreCliente.setValue('');
                            swal('Atención', 'No se encontraron resultados', 'info');
                        } else {
                            const cliente = data.cliente[0];
                            this.forma.controls.rutCliente.setValue(cliente.cli_nrutcliente + '' + cliente.cli_sdigverificador);
                            this.forma.controls.nombreCliente.setValue(cliente.cli_snombre);
                        }
                    } else {
                        this.forma.controls.rutCliente.setValue('');
                        this.forma.controls.nombreCliente.setValue('');
                        swal('Atención', 'Ha ocurrido un error inesperado!', 'error');
                    }

                    this.cargador.hide();
                },
                    error => {
                        this.forma.controls.rutCliente.setValue('');
                        this.forma.controls.nombreCliente.setValue('');
                        swal('Atención', 'Ha ocurrido un error inesperado!', 'error');
                        this.cargador.hide();
                    });
            return false;
        }
    }

    construirReporte(data: any) {
        const headerExcel = [
            'Nº Protesto',
            'Fecha Prot.',
            'Rut Cliente',
            'Nom Cliente',
            'Banco',
            'Nº Documento',
            'Motivo',
            'Obs.',
            'Monto',
            'Estado',
            'Fecha Liber.',
        ];

        const dataExcel = [];

        if (data.protesto.length > 0) {
            const sorted = data.protesto.sort((x, y) => {
                return x.pro_dfhoprotesto - y.pro_dfhoprotesto;
            });

            sorted.forEach(e => {
                const rut = e.cli_nrutcliente + '' + e.cli_sdigverificador;
                dataExcel.push([
                    e.pro_nidprotesto,
                    this.utilService.dateStringServiceToReverseStringFormat(e.pro_dfhoprotesto),
                    this.utilService.formateaRut(rut),
                    e.cli_snombre,
                    e.nombanco,
                    e.gar_niddocto,
                    e.nommotivo,
                    e.pro_sobservacion,
                    this.decimalPipe.transform(e.gar_nmonto, '', 'es'),
                    e.estado,
                    this.utilService.dateStringServiceToReverseStringFormat(e.pro_dfholiberacion.toString()),
                ]);
            });

            const total = data.protesto.reduce((acc, obj) => acc + obj.gar_nmonto, 0);

            dataExcel.push([
                '',
                'Total Protestos',
                data.protesto.length,
                this.decimalPipe.transform(total, '', 'es'),
            ]);
        } else {
            dataExcel.push([
                'NO SE ENCONTRARON DOCUMENTOS PROTESTADOS'
            ]);
        }


        const nombreArchivo = `Resumen_Protestos_${this.utilService.dateToStringDateFullFormat(new Date())}00`;
        const titulo = 'Informe de Caja Resumen de Protestos';
        const fechas = [
            `Fecha Desde: ${this.utilService.dateToLocalStringFormat(new Date(this.forma.controls.fechaDesde.value.replace(/-/g, '\/')))}`,
            `Fecha Hasta: ${this.utilService.dateToLocalStringFormat(new Date(this.forma.controls.fechaHasta.value.replace(/-/g, '\/')))}`,
        ];


        this.excelService.buildReportCajaCobranza(
            titulo,
            headerExcel,
            dataExcel,
            fechas,
            nombreArchivo
        );
    }

}
