import { Component, OnInit, OnDestroy } from '@angular/core';

import { DatePipe } from '@angular/common';
import { ExcelService } from '../../../core/excel/excel.service';
import { UtilService } from '../../../services/util.service';
import { CajaCobranzaService } from '../../../core/consultaService/cajaCobranza.service';
import { AuthService } from '../../../core/consultaService/auth.service';

import { NgxSpinnerService } from 'ngx-spinner';

const swal = require('sweetalert');

@Component({
    selector: 'app-resumen-cuentas-corrientes',
    templateUrl: './resumenCuentasCorrientes.component.html',
    styleUrls: ['./resumenCuentasCorrientes.component.scss'],
    providers: [DatePipe]
})
export class ResumenCuentasCorrientesComponent implements OnInit, OnDestroy {

    user: any = {};

    constructor(
        private excelService: ExcelService,
        private utilService: UtilService,
        private cajaCobranzaService: CajaCobranzaService,
        private authService: AuthService,
        public cargador: NgxSpinnerService) { }

    ngOnInit() {
        this.user = this.authService.getDatosUser();
        this.generateExcel();
    }

    ngOnDestroy() { }

    generateExcel() {
        this.cargador.show();
        const request = {
            nRutOperador: Number(this.user.nrutoperador)
        };
        this.cajaCobranzaService.getResumenCuentaCorriente(request).subscribe((data: any) => {
            this.cargador.hide();
            if (data) {
                if (data.error.codError !== 0) {
                    swal('Atención', 'Ha ocurrido un error inesperado!', 'error');
                    return;
                }
                if (data.resumenCTACTE.length === 0 && data.error.codError === 0) {
                    swal('Atención', 'No se encontraron resultados', 'info');
                } else {
                    // Generar Excel
                    this.construirReporte(data);
                }
            } else {
                swal('Atención', 'Ha ocurrido un error inesperado!', 'error');
            }
        },
            error => {
                this.cargador.hide();
                swal('Atención', 'Ha ocurrido un error inesperado!', 'error');
            });
    }

    construirReporte(data) {
        const headerExcel = [
            'ZONA',
            'TERRITORIO',
            'CORPORATIVO',
            'DIVISIONAL',
            'TOTAL',
        ];
        const dataExcel = [];
        data.resumenCTACTE.forEach(e => {
            dataExcel.push([
                e.ter_scodzona,
                e.ter_sdesterritorio,
                e.corporativo,
                e.divisional,
                e.corporativo + e.divisional,
            ]);
        });

        let additionalDataHeader = [];

        const otrosMontos = [];

        data.otrosMontos.forEach(e => {
            otrosMontos.push([
                e.smc_sdestipodocto,
                e.saldo,
            ]);
        });

        let corporativoPorcentaje = 0;
        let divPorcentaje = 0;

        if (data.nMontoVencidoCorp !== 0 && data.nMontoTotalCorp !== 0) {
            corporativoPorcentaje = data.nMontoVencidoCorp / data.nMontoTotalCorp;
        } else {
            corporativoPorcentaje = 0;
        }

        if (corporativoPorcentaje !== 0) {
            corporativoPorcentaje = corporativoPorcentaje * 100;
        } else {
            corporativoPorcentaje = 0;
        }

        if (data.nMontoVencidoDivi !== 0 && data.nMontoTotalDivi !== 0) {
            divPorcentaje = data.nMontoVencidoDivi / data.nMontoTotalDivi;
        } else {
            divPorcentaje = 0;
        }

        if (divPorcentaje !== 0) {
            divPorcentaje = divPorcentaje * 100;
        } else {
            divPorcentaje = 0;
        }



        additionalDataHeader = [
            {
                headerExcel: [
                    'DIAS CARTERA'
                ],
                dataExcel: [],
            },
            {
                headerExcel: [
                    '',
                    'DIAS CARTERA',
                    'PORCENTAJE VENCIDO',
                ],
                dataExcel: [
                    [
                        'CORPORATIVO',
                        data.nDiasCarteraCorp,
                        corporativoPorcentaje.toFixed(1) + ' %',
                    ],
                    [
                        'DIVISIONAL',
                        data.nDiasCarteraDivi,
                        divPorcentaje.toFixed(1) + ' %',
                    ],
                ],
            },
            {
                headerExcel: [
                ],
                dataExcel: [],
            },
            {
                headerExcel: [
                    'SALDO OTROS MOVIMIENTOS',
                ],
                dataExcel: [],
            },
            {
                headerExcel: [
                    'TIPO',
                    'MONTO',
                ],
                dataExcel: otrosMontos,
            },
            {
                headerExcel: [
                ],
                dataExcel: [],
            },
        ];

        const saldosPorVencer = [];
        data.saldoPorVencer.forEach(e => {
            saldosPorVencer.push([
                e.ter_scodzona,
                e.ter_sdesterritorio,
                e.corporativo,
                e.divisional,
                e.corporativo + e.divisional
            ]);
        });
        saldosPorVencer.push([]);

        const saldosVencidos = [];
        data.saldosVencidos.forEach(e => {
            saldosVencidos.push([
                e.ter_scodzona,
                e.ter_sdesterritorio,
                e.rg1_corporativo,
                e.rg1_divisional,
                e.rg2_corporativo,
                e.rg2_divisional,
                e.rg3_corporativo,
                e.rg3_divisional,
                e.rg4_corporativo,
                e.rg4_divisional,
                e.rg5_corporativo,
                e.rg5_divisional,
                e.rg6_corporativo,
                e.rg6_divisional,
            ]);
        });
      

        const additionalDataBody = [
            {
                headerExcel: [
                    'SALDOS POR VENCER FACTURAS'
                ],
                dataExcel: [],
            },
            {
                headerExcel: [
                    'ZONA',
                    'TERRITORIO',
                    'CORPORATIVO',
                    'DIVISIONAL',
                    'MONTO'
                ],
                dataExcel: saldosPorVencer,
            },
            {
                headerExcel: [
                    'VENCIDO REAL FACTURAS'
                ],
                dataExcel: [],
            },
            {
                headerExcel: [
                    'ZONA',
                    'TERRITORIO',
                    '1-5 DIAS CORPORATIVO',
                    '1-5 DIAS DIVISIONAL',
                    '6-10 DIAS CORPORATIVO',
                    '6-10 DIAS DIVISIONAL',
                    '11-20 DIAS CORPORATIVO',
                    '11-20 DIAS DIVISIONAL',
                    '21-30 DIAS CORPORATIVO',
                    '21-30 DIAS DIVISIONAL',
                    '31-60 DIAS CORPORATIVO',
                    '31-60 DIAS DIVISIONAL',
                    '61 Ó MÁS DIAS CORPORATIVO',
                    '61 Ó MÁS DIAS DIVISIONAL',

                ],
                dataExcel: saldosVencidos,
            },
        ];


        const nombreArchivo = `Resumen_CtasCtes_${this.utilService.dateToStringDateFullFormat(new Date())}00`;
        const titulo = 'Reporte Resumen Cuentas Corrientes';
        const fechas = [
            `Fecha: ${this.utilService.dateToLocalStringFormat(new Date())}`,
        ];

        this.excelService.buildReportCajaCobranza(
            titulo,
            headerExcel,
            dataExcel,
            fechas,
            nombreArchivo,
            null,
            additionalDataBody,
            additionalDataHeader
        );

    }

}
