import { Component, OnInit } from '@angular/core';
import { UtilService } from '../../../../../services/util.service';

import { FormGroup, FormBuilder, Validators, FormControl } from '@angular/forms';
import { NgxSpinnerService } from 'ngx-spinner';
import { registerLocaleData } from '@angular/common';
import es from '@angular/common/locales/es';
import { BsModalService, BsModalRef } from 'ngx-bootstrap/modal';


const swal = require('sweetalert');

import { ClientesService } from '../../../../../core/consultaService/clientes.service';
import { CajaCobranzaService } from '../../../../../core/consultaService/cajaCobranza.service';
import { DocumentosAsociadosPopUpComponent } from '../../../../commonPopUp/documentosAsociadosPopUp/documentosAsociadosPopUp.component';
import { DetalleFacturaPopUpComponent } from '../../../../commonPopUp/detalleFacturaPopUp/detalleFacturaPopUp.component';
import { ExcelService } from '../../../../../core/excel/excel.service';


@Component({
  selector: 'app-periodo',
  templateUrl: './periodo.component.html',
  styleUrls: ['./periodo.component.scss']
})
export class PeriodoComponent implements OnInit {

  forma: FormGroup;
  fechaActual = new Date();

  request: any = {};
  bsModalRef: BsModalRef;

  movimientos: Array<any> = [];
  pageActualPeriodo = 1;

  opcion: string;
  verData: boolean;
  saldoAnterior = 0;
  fechaOperacionSaldo: Date;

  constructor(
    private utilService: UtilService,
    private clientesService: ClientesService,
    private cargador: NgxSpinnerService,
    private cajaCobranzaService: CajaCobranzaService,
    private modalService: BsModalService,
    private excelService: ExcelService,
  ) {
    this.forma = new FormGroup({
      fechaDesde: new FormControl(''),
      fechaHasta: new FormControl('')
    });
  }

  ngOnInit() {
    this.forma.controls.fechaDesde.setValue(this.utilService.dateToStringFormat(this.fechaActual));
    this.forma.controls.fechaHasta.setValue(this.utilService.dateToStringFormat(this.fechaActual));
  }

  actualizarFecha(fecha) {
    if (fecha.value === '') {
      fecha.value = this.utilService.dateToStringFormat(this.fechaActual);
    }

    if (!this.utilService.DateValidator(fecha.value)) {
      fecha.value = this.utilService.dateToStringFormat(this.fechaActual);
    }
  }

  verDetallePago(movimiento) {

    const initialState = {
      titulo: 'Documentos Asociados',
      cliente: this.clientesService.selectedCliente,
      movimiento: movimiento
    };

    this.bsModalRef = this.modalService.show(DocumentosAsociadosPopUpComponent, { initialState, backdrop: 'static', keyboard: false });
  }

  verDetalleFactura(movimiento) {

    const initialState = {
      titulo: 'Detalle Factura',
      cliente: this.clientesService.selectedCliente,
      movimiento: movimiento
    };

    this.bsModalRef = this.modalService.show(DetalleFacturaPopUpComponent, { initialState, backdrop: 'static', keyboard: false });
  }

  validarForm() {

    if (this.forma.controls.fechaDesde.value !== '' && this.forma.controls.fechaHasta.value !== '') {
      if (new Date(this.forma.controls.fechaDesde.value) > new Date(this.forma.controls.fechaHasta.value)) {
        swal('Atención', 'Periodo Desde debe ser menor a la Periodo Hasta!', 'error');
        return true;
      }
      if (new Date(this.forma.controls.fechaHasta.value) < new Date(this.forma.controls.fechaDesde.value)) {
        swal('Atención', 'Periodo Hasta debe ser mayor a la Periodo Desde!', 'error');
        return true;
      }
    } else {
      swal('Atención', 'Seleccione un periodo de fecha!', 'error');
      return true;
    }

    return false;
  }

  buscar() {
    this.opcion = 'buscar';
    this.verData = false;
    this.buscaCuentasCorrientesPeriodo();
  }

  buscaCuentasCorrientesPeriodo() {

    if (this.validarForm()) {
      return;
    }
    this.fechaOperacionSaldo = new Date(this.forma.controls.fechaDesde.value);

    this.request.fechaDesde = (this.forma.controls.fechaDesde.value).replace(/-/g, ''),
      this.request.fechaHasta = (this.forma.controls.fechaHasta.value).replace(/-/g, ''),
      this.request.nRutCliente = this.clientesService.selectedCliente.cli_nrutcliente;
    this.request.nRutOperador = this.clientesService.selectedCliente.cli_nrutoperador;

    this.cargador.show();
    this.cajaCobranzaService.buscaCuentasCorrientesPeriodo(this.request)
      .subscribe((data: any) => {
        this.cargador.hide();
        this.verData = true;
        if (data) {
          if (data.movimientos.length > 0 && data.error.codError === 0) {
            this.saldoAnterior = 0;

            const fechaDesde = Number(this.request.fechaDesde.replace(/-/g, '') + '000000');
            const fechaHasta = Number(this.request.fechaHasta.replace(/-/g, '') + '000000');

            const filteredData = data.movimientos.filter(e => {
              return Number(e.cte_dfhocreacion) >= fechaDesde && Number(e.cte_dfhocreacion) <= fechaHasta;
            });

            const filteredFechaMenor = data.movimientos.filter(e => {
              return Number(e.cte_dfhocreacion) < fechaDesde;
            });

            if (filteredFechaMenor.length > 0) {
              for (let i = 0; i < filteredFechaMenor.length; i++) {
                const e = filteredFechaMenor[i];
                this.saldoAnterior = this.saldoAnterior + e.cte_ncargo - e.cte_nabono;
              }
            }

            this.cargarMovimientosArray(filteredData, this.saldoAnterior);
            if (this.opcion === 'exportar') {
              this.construirReporte(this.movimientos);
            }
          } else {
            this.movimientos = [];
            swal('Atención', 'No se encontraron resultados', 'info');
          }

          if (data.error.codError !== 0) {
            swal('Atención', 'Ha ocurrido un error inesperado!', 'error');
          }
        }
      },
        error => {
          this.cargador.hide();
          swal('Atención', 'Ha ocurrido un error inesperado!', 'error');
        });
  }

  construirReporte(data: any) {
    const headerExcel = [
      'Fecha Operación',
      'Nº Operación',
      'Tipo Operación',
      'Tipo Documento',
      'Nro. Documento',
      'Observación',
      'Cargo',
      'Abono',
      'Saldo',
    ];

    const dataExcel = [];

    dataExcel.push([
      this.utilService.dateToLocalStringFormat(this.fechaOperacionSaldo),
      '',
      '',
      '',
      '',
      '',
      this.saldoAnterior > 0 ? this.saldoAnterior : 0,
      this.saldoAnterior < 0 ? (this.saldoAnterior * -1) : 0,
      this.saldoAnterior,
    ]);

    data.forEach(e => {
      dataExcel.push([
        this.utilService.dateStringServiceToLocalStringFormat(e.cte_dfhocreacion),
        e.cte_nidoperacion,
        e.cte_scodtipooperacion,
        e.cte_scodtipodocto,
        e.nnumdocto,
        e.cte_sglosa,
        e.cte_ncargo,
        e.cte_nabono,
        e.nSaldoAnterior,
      ]);
    });

    const nombreArchivo = `Cuentas_Periodo_${this.utilService.dateToStringDateFullFormat(new Date())}00`;
    const titulo = 'Listado Cuentas Periodo';
    const fechas = [
      `Fecha Desde: ${this.utilService.dateToLocalStringFormat(new Date(this.forma.controls.fechaDesde.value.replace(/-/g, '\/')))}`,
      `Fecha Hasta: ${this.utilService.dateToLocalStringFormat(new Date(this.forma.controls.fechaHasta.value.replace(/-/g, '\/')))}`,
    ];

    this.excelService.buildReportCajaCobranza(
      titulo,
      headerExcel,
      dataExcel,
      fechas,
      nombreArchivo
    );
  }
  exportar() {
    this.opcion = 'exportar';
    this.buscaCuentasCorrientesPeriodo();
  }

  cargarMovimientosArray(data, saldoAnterior) {
    this.movimientos = [];
    let saldo = saldoAnterior;

    data.forEach(e => {
      let observacion = e.cte_sglosa.trim();

      if (e.sinddocumentado === 'S') {
        if (observacion === '') {
          observacion = 'Factura Documentada';
        } else {
          observacion += ' - Factura Documentada';
        }
      }

      saldo = saldo + (e.cte_ncargo - e.cte_nabono);
      const movimiento = {
        cte_dfhocreacion: e.cte_dfhocreacion,
        cte_nidoperacion: e.cte_nidoperacion,
        cte_scodtipooperacion: e.cte_scodtipooperacion,
        cte_sdestipooperacion: e.cte_sdestipooperacion,
        cte_scodtipodocto: e.cte_scodtipodocto,
        cte_sdestipodocto: e.cte_sdestipodocto,
        cte_niddocto: e.cte_niddocto,
        nnumdocto: e.nnumdocto,
        cte_sglosa: observacion,
        cte_ncargo: e.cte_ncargo,
        cte_nabono: e.cte_nabono,
        sinddocasociados: e.sinddocasociados,
        nSaldoAnterior: saldo,
      };
      this.movimientos.push(movimiento);
    });
  }

}
