import { Component, OnInit, OnDestroy } from '@angular/core';
import { BsModalService, BsModalRef } from 'ngx-bootstrap/modal';
import { ConsultasService } from '../../../core/consultaService/consultas.service';
import { ValidacionUsuarioPopUpComponent } from '../../commonPopUp/validacionUsuarioPopUp/validacionUsuarioPopUp.component';
import { AuthService } from '../../../core/consultaService/auth.service';
import { DatePipe } from '@angular/common';

const swal = require('sweetalert');

@Component({
  selector: 'app-cierre-caja',
  templateUrl: './cierreCaja.component.html',
  styleUrls: ['./cierreCaja.component.scss'],
  providers: [DatePipe]
})
export class CierreCajaComponent implements OnInit, OnDestroy {

  bsModalRef: BsModalRef;
  user: any = {};
  fechaProceso: string;
  subDataCliente: any;

  constructor(
    private modalService: BsModalService,
    private consultasService: ConsultasService,
    private authService: AuthService) { }

  ngOnInit() {
    this.user = this.authService.getDatosUser();

    this.subDataCliente = this.consultasService.datos$.subscribe(resData => {
      this.setData(resData);
    });

    this.getFechaProceso();
  }

  ngOnDestroy() {
    if (this.subDataCliente !== undefined) {
      this.subDataCliente.unsubscribe();
    }
  }

  openModal() {
    const initialState = {
      titulo: 'Validación de Cierre',
    };
    this.bsModalRef = this.modalService.show(ValidacionUsuarioPopUpComponent, { initialState, backdrop: 'static', keyboard: false });
  }

  getFechaProceso() {
    const request = {
      nRutOperador: Number(this.user.nrutoperador),
      sCodProceso: 'CAJA'
    };

    this.consultasService.getFechaProceso(request)
      .subscribe((data: any) => {
        if (data) {
          if (data.error.codError !== 0) {
            swal('Atención', 'Ha ocurrido un error inesperado!', 'error');
            return;
          }
          if (data.fechaProceso.length === 0 && data.error.codError === 0) {
            swal('Atención', 'No se encontraron resultados', 'info');
          } else {
            const fechaProceso = data.fechaProceso[0];
            this.fechaProceso = this.convertirFecha(fechaProceso.fpr_dfhoproceso);
          }
        } else {
          swal('Atención', 'Ha ocurrido un error inesperado!', 'error');
        }
      });
  }

  convertirFecha(fecha) {
    if (fecha) {
      const regexp = /(\d{4})(\d{2})(\d{2})$/;
      const tag = fecha.match(regexp);
      return tag[3] + '/' + tag[2] + '/' + tag[1];
    }
    return '';
  }

  setData(data) {
    this.getFechaProceso();
  }

}
