import { Component, OnInit, OnDestroy } from '@angular/core';
import * as _ from 'lodash';
import { FormGroup, FormBuilder, Validators, FormControl } from '@angular/forms';
import { BsModalService, BsModalRef } from 'ngx-bootstrap/modal';
import { NgxSpinnerService } from 'ngx-spinner';

import { AuthService } from '../../../core/consultaService/auth.service';
import { UtilService } from './../../../services/util.service';
import { ConsultasService } from '../../../core/consultaService/consultas.service';
import { ExcelService } from '../../../core/excel/excel.service';
import { ZonasTodasPopUpComponent } from '../../commonPopUp/zonasTodasPopUp/zonasTodasPopUp.component';
import { RutAsociadoPopUpComponent } from '../../commonPopUp/rutAsociadoPopUp/rutAsociadoPopUp.component';
import { CajaCobranzaService } from '../../../core/consultaService/cajaCobranza.service';
import { ClientesService } from '../../../core/consultaService/clientes.service';

const swal = require('sweetalert');

@Component({
  selector: 'app-partidas-abiertas',
  templateUrl: './partidasAbiertas.component.html',
  styleUrls: ['./partidasAbiertas.component.scss']
})
export class PartidasAbiertasComponent implements OnInit, OnDestroy {

  forma: FormGroup;
  request: any = {};
  origen: string;
  user: any = {};
  territoriosTodosModal: BsModalRef;
  clienteModal: BsModalRef;
  subDataCliente: any;
  subDataGeo: any;
  tipoPagos: Array<any>;

  constructor(
    private modalService: BsModalService,
    private authService: AuthService,
    public loader: NgxSpinnerService,
    private utilService: UtilService,
    private setearDatos: ConsultasService,
    private clientesService: ClientesService,
    private excelService: ExcelService,
    private cajaCobranzaService: CajaCobranzaService
  ) {

    this.forma = new FormGroup({
      nRutCliente: new FormControl(''),
      nombreCliente: new FormControl({ value: '', disabled: true }),
      sCodTerritorio: new FormControl({ value: '', disabled: true }),
      descTerritorio: new FormControl({ value: '', disabled: true }),
      tipoPago: new FormControl(''),
    });
  }

  ngOnInit() {
    this.user = this.authService.getDatosUser();

    this.subDataCliente = this.setearDatos.datos$.subscribe(resData => {
      this.setData(resData);
    });

    this.subDataGeo = this.setearDatos.datosGeo$.subscribe(resData => {
      this.setData(resData);
    });

    this.getTiposPagoReversa();

  }

  ngOnDestroy() {
    if (this.subDataCliente !== undefined) {
      this.subDataCliente.unsubscribe();
    }
    if (this.subDataGeo !== undefined) {
      this.subDataGeo.unsubscribe();
    }
  }

  openModalTerritorios() {
    this.origen = 'territoriosPedidos';
    const initialState = {
      parametro: this.user.nrutoperador,
      origen: 'territoriosPedidos'
    };
    this.territoriosTodosModal = this.modalService.show(ZonasTodasPopUpComponent, { initialState, backdrop: 'static', keyboard: false });
  }

  openModalCliente() {
    this.origen = 'clientePedido';
    const initialState = {
      titulo: 'Cliente',
      origen: 'clientePedido'
    };
    this.clienteModal = this.modalService.show(RutAsociadoPopUpComponent, { initialState, backdrop: 'static', keyboard: false });
  }

  validaRut() {

    if (this.forma.controls.nRutCliente.value === '') {
      return;
    }

    if (!this.utilService.isValidRUT(this.forma.controls.nRutCliente.value)) {
      this.forma.controls.nRutCliente.setValue('');
      this.forma.controls.nombreCliente.setValue('');
      swal('Atención', 'El RUT es incorrecto', 'error');
      return true;
    } else {
      this.loader.show();

      let rutCliente = this.forma.controls.nRutCliente.value;

      rutCliente = rutCliente.substring(0, rutCliente.length - 1);

      const request = {
        rutClientes: Number(rutCliente),
        rutOperador: Number(this.user.nrutoperador),
      };

      this.clientesService.getDetallesClientes(request)
        .subscribe((data: any) => {
          this.loader.hide();
          if (data) {
            if (data.error.codError !== 0) {
              this.forma.controls.nRutCliente.setValue('');
              this.forma.controls.nombreCliente.setValue('');
              swal('Atención', 'Ha ocurrido un error inesperado!', 'error');
              return;
            }
            if (data.cliente.length === 0 && data.error.codError === 0) {
              this.forma.controls.nRutCliente.setValue('');
              this.forma.controls.nombreCliente.setValue('');
              swal('Atención', 'No se encontraron resultados', 'info');
            } else {
              const cliente = data.cliente[0];
              this.forma.controls.nRutCliente.setValue(cliente.cli_nrutcliente + cliente.cli_sdigverificador);
              this.forma.controls.nombreCliente.setValue(cliente.cli_snombre);
            }
          } else {
            swal('Atención', 'Ha ocurrido un error inesperado!', 'error');
          }
        },
          error => {
            this.loader.hide();
            swal('Atención', 'Ha ocurrido un error inesperado!', 'error');
            this.forma.controls.nRutCliente.setValue('');
            this.forma.controls.nombreCliente.setValue('');
          });
      return false;
    }

  }

  getTiposPagoReversa() {
    this.cajaCobranzaService.getTiposPagoReversa()
      .subscribe((data: any) => {
        if (data) {
          if (data.tipoPago.length === 0 && data.error.codError === 0) {
            swal('Atención', 'No se encontraron resultados', 'info');
          } else {
            this.tipoPagos = data.tipoPago;
            this.tipoPagos.unshift({
              par_codparametro01: '',
              par_deslargo01: '(Ninguno)'
            });
          }
        }
      },
        error => {
        });
  }

  buscar() {

    this.loader.show();
    const request = {
      nRutCliente: this.forma.controls.nRutCliente.value === '' ? 0 : Number(this.forma.controls.nRutCliente.value.slice(0, -1)),
      nRutOperador: Number(this.user.nrutoperador),
      sCodTerritorio: this.forma.controls.sCodTerritorio.value === '' ? null : this.forma.controls.sCodTerritorio.value,
      sCodTipoCobro: this.forma.controls.tipoPago.value === '' ? null : this.forma.controls.tipoPago.value
    };
    this.cajaCobranzaService.getPartidasAbiertas(request)
      .subscribe((data: any) => {
        this.loader.hide();
        if (data) {
          if (data.error.codError !== 0) {
            swal('Atención', 'Ha ocurrido un error inesperado!', 'error');
            return;
          }
          if (data.partidasAbiertas.length === 0 && data.error.codError === 0) {
            swal('Atención', 'No se encontraron documentos protestados', 'info');
          } else {
            // Generar Excel
            this.construirReporte(data);
          }
        } else {
          swal('Atención', 'Ha ocurrido un error inesperado!', 'error');
        }

      },
        error => {
          this.loader.hide();
          swal('Atención', 'Ha ocurrido un error inesperado!', 'error');
        });
  }

  setData(resData: any) {
    if (resData) {
      switch (this.origen) {
        case 'clientePedido': {
          this.forma.controls.nRutCliente.setValue(resData.cli_nrutcliente + resData.cli_sdigverificador);
          this.forma.controls.nombreCliente.setValue(resData.cli_snombre);
          break;
        }
        case 'territoriosPedidos': {
          this.forma.controls.sCodTerritorio.setValue(resData.ter_scodterritorio);
          this.forma.controls.descTerritorio.setValue(resData.ter_sdesterritorio);
          break;
        }
      }
    }
  }

  limpiarTerritorio() {
    this.forma.controls.sCodTerritorio.setValue('');
    this.forma.controls.descTerritorio.setValue('');
  }

  construirReporte(data: any) {
    const headerExcel = [
      'RUT CLIENTE',
      'Nº LOCAL',
      'NOMBRE CLIENTE',
      'TIPO DOCUMENTO',
      'OBSERVACION',
      'Nº DOCUMENTO',
      'SALDO',
      'CONDICION DE PAGO',
      'FECHA EMISION',
      'FECHA VENCIMIENTO REAL',
      'TRAMO VCTO',
      'NOMBRE ZONA',
      'CODIGO SUPERVISOR',
      'NOMBRE SUPERVISOR',
      'CODIGO TERRITORIO',
      'NOMBRE TERRITORIO'
    ];

    const dataExcel = [];

    data.partidasAbiertas.forEach(e => {
      dataExcel.push([
        e.rutCliente,
        e.numLocal,
        e.nomcliente,
        e.codtipodocto,
        '',
        e.numDocto,
        e.saldo,
        e.desformapago,
        this.convertirFecha(e.fechaemision),
        this.convertirFecha(e.fechavencimiento),
        e.tramo,
        e.deszona,
        e.ter_scodsupervisor,
        e.sup_snombre,
        e.codterritorio,
        e.desterritorio
      ]);
    });


    this.excelService.generateExcel(
      'Partidas Abiertas',
      headerExcel, dataExcel,
      this.utilService.dateToLocalStringFormat(new Date()));
  }

  convertirFecha(fecha) {
    if (fecha) {
      const regexp = /(\d{4})(\d{2})(\d{2})$/;
      const tag = fecha.match(regexp);
      return tag[3] + '/' + tag[2] + '/' + tag[1];
    }
    return '';
  }

  isNumberKey(evt) {
    const charCode = (evt.which) ? evt.which : evt.keyCode;
    if (charCode > 31 && (charCode < 48 || charCode > 57)) {
      return false;
    }
    return true;
  }

}
