import { Component, OnInit, OnDestroy } from '@angular/core';
import { FormGroup, FormBuilder, Validators, FormControl } from '@angular/forms';
import { CajaCobranzaService } from '../../../core/consultaService/cajaCobranza.service';
import { AuthService } from '../../../core/consultaService/auth.service';
import { NgxSpinnerService } from 'ngx-spinner';
import { DatePipe } from '@angular/common';
import { UtilService } from './../../../services/util.service';
import { ExcelService } from '../../../core/excel/excel.service';

import { registerLocaleData, Time } from '@angular/common';
import es from '@angular/common/locales/es';

import { DecimalPipe } from '@angular/common';

const swal = require('sweetalert');

@Component({
    selector: 'app-resumen-documentos-cobranza',
    templateUrl: './resumenDocumentosCobranza.component.html',
    styleUrls: ['./resumenDocumentosCobranza.component.scss'],
    providers: [DatePipe]
})
export class ResumenDocumentosCobranzaComponent implements OnInit, OnDestroy {

    forma: FormGroup;
    user: any = {};
    fechaActual = new Date();

    constructor(
        private cajaCobranzaService: CajaCobranzaService,
        private authService: AuthService,
        public cargador: NgxSpinnerService,
        private utilService: UtilService,
        private excelService: ExcelService,
        private decimalPipe: DecimalPipe,
    ) {
        this.forma = new FormGroup({
            fecha: new FormControl('', Validators.required),
        });
    }

    ngOnInit() {
        this.user = this.authService.getDatosUser();
        this.forma.controls.fecha.setValue(this.utilService.dateToStringFormat(this.fechaActual));
        registerLocaleData(es);
    }

    ngOnDestroy() { }

    buscar() {
        if (this.validarFormulario()) {
            return;
        }
        this.cargador.show();

        const request = {
            nRutOperador: this.user.nrutoperador,
            sFecha: (this.forma.controls.fecha.value).replace(/-/g, ''),
            sCodSucursal: null,
        };
        this.cajaCobranzaService.buscaRptResumenLiquidacion(request)
            .subscribe((data: any) => {
                this.cargador.hide();
                if (data) {
                    if (data.error.codError !== 0) {
                        swal('Atención', 'Ha ocurrido un error inesperado!', 'error');
                        return;
                    }
                    if (data.resumen.length === 0 && data.error.codError === 0) {
                        // Generar Excel
                        this.construirReporte(data);
                    } else {
                        // Generar Excel
                        this.construirReporte(data);
                    }
                } else {
                    swal('Atención', 'Ha ocurrido un error inesperado!', 'error');
                }
            },
                error => {
                    this.cargador.hide();
                    swal('Atención', 'Ha ocurrido un error inesperado!', 'error');
                });
    }

    validarFormulario() {
        if (this.forma.controls.fecha.value === '') {
            swal('Atención', 'Campo fecha requerido', 'error');
            return true;
        }
        return false;
    }

    construirReporte(data: any) {
        const headerExcel = [
            'Sucursal',
            'Id Recorrido',
            'Efectivo',
            'Cheque al día',
            'Cheque a fecha',
            'Aplicación Cré',
            'Crédito Directo',
            'Totales',
        ];

        const dataExcel = [];

        data.resumen.forEach(e => {
            const total = e.efectivo + e.cheque + e.chequefecha + e.creditoaplicado + e.creditodirecto;
            dataExcel.push([
                e.suc_sdescsucursal,
                e.rlr_nidrecorrido,
                e.efectivo,
                e.cheque,
                e.chequefecha,
                e.creditoaplicado,
                e.creditodirecto,
                total,
            ]);
        });

        const additionalData = [];

        if (data.otros.length > 0) {
            const dataAdicional = [];

            data.otros.forEach(e => {
                dataAdicional.push([
                    e.rpa_sdestipopago,
                    '',
                    e.nmontopago,
                    e.nmontopago
                ]);
            });

            dataAdicional.push([]);
            const subtotal = data.otros.reduce((acc, obj) => acc + obj.nmontopago, 0);
            const total = data.otros.reduce((acc, obj) => acc + obj.nmontopago, 0);

            dataAdicional.push([
                'Total',
                0,
                this.decimalPipe.transform(subtotal, '', 'es'),
                this.decimalPipe.transform(total, '', 'es'),
            ]);
            const item = {
                headerExcel: [
                    'Forma Pago',
                    'Liquidación',
                    'Otros Pagos',
                    'SubTotal',
                ],
                dataExcel: dataAdicional,
            };

            additionalData.push(item);
        }

        const nombreArchivo = `Resumen_Liquidacion_${this.utilService.dateToStringDateFullFormat(new Date())}00`;
        const titulo = 'Informe de Caja Resumen Liquidación Recaudación';
        const fechas = [
            `Fecha de Caja: ${this.utilService.dateToLocalStringFormat(new Date(this.forma.controls.fecha.value.replace(/-/g, '\/')))}`,
        ];

        this.excelService.buildReportCajaCobranza(
            titulo,
            headerExcel,
            dataExcel,
            fechas,
            nombreArchivo,
            null,
            additionalData);
    }

}
