import { Component, OnInit, OnDestroy } from '@angular/core';
import * as _ from 'lodash';
import { FormGroup, FormBuilder, Validators, FormControl } from '@angular/forms';
import { BsModalService, BsModalRef } from 'ngx-bootstrap/modal';
import { NgxSpinnerService } from 'ngx-spinner';

import { AuthService } from '../../../core/consultaService/auth.service';
import { UtilService } from './../../../services/util.service';
import { CajaCobranzaService } from '../../../core/consultaService/cajaCobranza.service';
import { ExcelService } from '../../../core/excel/excel.service';
import { BuscaTransportistaPopUpComponent } from '../../commonPopUp/buscaTransportistaPopUp/buscaTransportistaPopUp.component';
import { ConsultasService } from '../../../core/consultaService/consultas.service';
import { AdministracionOperativaService } from '../../../core/consultaService/administracionOperativa.service';

const swal = require('sweetalert');

@Component({
  selector: 'app-partidas-abiertas-transportista',
  templateUrl: './partidasAbiertasTransportista.component.html',
  styleUrls: ['./partidasAbiertasTransportista.component.scss']
})
export class PartidasAbiertasTransportistaComponent implements OnInit, OnDestroy {

  forma: FormGroup;
  bsModalRef: BsModalRef;
  user: any = {};
  request: any = {};
  subData: any;

  constructor(
    private modalService: BsModalService,
    private utilService: UtilService,
    private cajaCobranzaService: CajaCobranzaService,
    private authService: AuthService,
    private consultasService: ConsultasService,
    public loader: NgxSpinnerService,
    private excelService: ExcelService,
    private administracionOperativaService: AdministracionOperativaService
  ) {
    this.forma = new FormGroup({
      rutTransportista: new FormControl({ value: '' }),
      nombreTransportista: new FormControl({ value: '', disabled: true }),
    });
  }

  ngOnInit() {
    this.user = this.authService.getDatosUser();
    this.subData = this.consultasService.datos$.subscribe(resData => {
      this.setData(resData);
    });

  }

  ngOnDestroy() {
    if (this.subData !== undefined) {
      this.subData.unsubscribe();
    }
  }

  openModal() {
    const initialState = {
      titulo: 'Transportista',
      // origen: 'linea'
    };
    this.bsModalRef = this.modalService.show(BuscaTransportistaPopUpComponent, { initialState, backdrop: 'static', keyboard: false });
  }

  validarForm() {
    if (this.forma.controls.rutTransportista.value === '' || this.forma.controls.rutTransportista.value === undefined) {
      swal('Atención', 'Campo Transportista es requerido', 'info');
      return true;
    }
    return false;
  }

  validaRut() {

    if (this.forma.controls.rutTransportista.value === '') {
      return;
    }

    if (!this.utilService.isValidRUT(this.forma.controls.rutTransportista.value)) {
      this.forma.controls.rutTransportista.setValue('');
      this.forma.controls.nombreTransportista.setValue('');
      swal('Atención', 'El RUT es incorrecto', 'error');
      return true;
    } else {
      this.loader.show();

      let rutTransportista = this.forma.controls.rutTransportista.value;

      rutTransportista = rutTransportista.substring(0, rutTransportista.length - 1);

      const request = {
        rutClientes: Number(rutTransportista),
        rutOperador: Number(this.user.nrutoperador),
      };
      this.loader.hide();

      this.request.rutTransportista = Number(rutTransportista);
      this.request.rutOperador = Number(this.user.nrutoperador);

      this.loader.show();

      this.administracionOperativaService.getTransportista(this.request)
        .subscribe((data: any) => {
          this.loader.hide();
          if (data) {
            if (data.buscaTransportistaDTO.length > 0 && data.error.codError === 0) {

              const transportista = data.buscaTransportistaDTO[0];
              this.forma.controls.rutTransportista.setValue(transportista.tra_nruttransportista + transportista.tra_sdigitoverificador);
              this.forma.controls.nombreTransportista.setValue(transportista.tra_srazonsocial);

            } else if (data.buscaTransportistaDTO.length === 0 && data.error.codError === 0) {

              this.forma.controls.rutTransportista.setValue('');
              this.forma.controls.nombreTransportista.setValue('');
              swal('Atención', 'No se encontraron resultados!', 'error');

            } else {
              this.forma.controls.rutTransportista.setValue('');
              this.forma.controls.nombreTransportista.setValue('');
              swal('Atención', 'Ha ocurrido un error inesperado!', 'error');
            }
          } else {
            this.forma.controls.rutTransportista.setValue('');
            this.forma.controls.nombreTransportista.setValue('');
            swal('Atención', 'Ha ocurrido un error inesperado!', 'error');
          }
        },
          error => {
            this.forma.controls.rutTransportista.setValue('');
            this.forma.controls.nombreTransportista.setValue('');
            this.bsModalRef.hide();
            swal('Atención', 'Ha ocurrido un error inesperado!', 'error');
          });

    }
  }

  buscar() {
    if (this.validarForm()) {
      return;
    }
    this.loader.show();
    const request = {
      nRutOperador: Number(this.user.nrutoperador),
      nRutTransportista: this.forma.controls.rutTransportista.value === '' ? 0 : Number(this.forma.controls.rutTransportista.value.slice(0, -1)),
      sCodTipoCobro: null
    };
    this.cajaCobranzaService.getPartidasAbiertasTransportista(request)
      .subscribe((data: any) => {
        this.loader.hide();
        if (data) {
          if (data.error.codError !== 0) {
            swal('Atención', 'Ha ocurrido un error inesperado!', 'error');
            return;
          }
          if (data.partidadsAbiertas.length === 0 && data.error.codError === 0) {
            swal('Atención', 'No se encontraron documentos protestados', 'info');
          } else {
            // Generar Excel
            this.construirReporte(data);
          }
        } else {
          swal('Atención', 'Ha ocurrido un error inesperado!', 'error');
        }
      },
        error => {
          this.loader.hide();
        });
  }

  setData(data) {
    if (data) {
      this.forma.controls.rutTransportista.setValue(data.tra_nruttransportista + data.tra_sdigitoverificador);
      this.forma.controls.nombreTransportista.setValue(data.tra_srazonsocial);
    }
  }

  construirReporte(data: any) {
    const headerExcel = [
      'RUT TRANSPORTISTA',
      'NOMBRE TRANSPORTISTA',
      'TIPO DOCUMENTO',
      'OBSERVACION',
      'Nº DOCUMENTO',
      'SALDO',
      'CONDICION DE PAGO',
      'FECHA EMISION',
      'FECHA VENCIMIENTO REAL',
    ];

    const dataExcel = [];

    data.partidadsAbiertas.forEach(e => {
      dataExcel.push([
        e.ruttransportista,
        e.nomtransportista,
        e.codtipodocto,
        '',
        e.numdocto,
        e.saldo,
        e.desformapago,
        this.convertirFecha(e.fechaemision),
        this.convertirFecha(e.fechavencimiento),
      ]);
    });


    const nombreArchivo = `Partidas Abiertas ${this.utilService.dateToStringDateFullFormat(new Date())}00`;
    const titulo = 'Reporte Partidas Abiertas';
    const fechas = [
      `Fecha: ${this.utilService.dateToLocalStringFormat(new Date())}`,
    ];

    this.excelService.buildReportCajaCobranza(
      titulo,
      headerExcel,
      dataExcel,
      fechas,
      nombreArchivo
    );
  }

  convertirFecha(fecha) {
    if (fecha) {
      const regexp = /(\d{4})(\d{2})(\d{2})$/;
      const tag = fecha.match(regexp);
      return tag[3] + '/' + tag[2] + '/' + tag[1];
    }
    return '';
  }

}
