import { Component, OnInit } from '@angular/core';
import { FormGroup, FormBuilder, Validators, FormControl } from '@angular/forms';
import { NgxSpinnerService } from 'ngx-spinner';
import { registerLocaleData } from '@angular/common';
import es from '@angular/common/locales/es';
import { BsModalService, BsModalRef } from 'ngx-bootstrap/modal';
import { UtilService } from '../../../../../services/util.service';

const swal = require('sweetalert');

import { ClientesService } from '../../../../../core/consultaService/clientes.service';
import { CajaCobranzaService } from '../../../../../core/consultaService/cajaCobranza.service';
import { DetalleProtestoPopUpComponent } from '../../../../commonPopUp/detalleProtestoPopUp/detalleProtestoPopUp.component';
import { DetalleFacturaPopUpComponent } from '../../../../commonPopUp/detalleFacturaPopUp/detalleFacturaPopUp.component';
import { ExcelService } from '../../../../../core/excel/excel.service';
import { DecimalPipe } from '@angular/common';

@Component({
  selector: 'app-pendientes',
  templateUrl: './pendientes.component.html',
  styleUrls: ['./pendientes.component.scss']
})
export class PendientesComponent implements OnInit {

  forma: FormGroup;
  request: any = {};
  bsModalRef: BsModalRef;

  movimientos: Array<any> = [];
  pageActualPendiente = 1;
  opcion: string;

  constructor(
    private clientesService: ClientesService,
    private cargador: NgxSpinnerService,
    private cajaCobranzaService: CajaCobranzaService,
    private modalService: BsModalService,
    private excelService: ExcelService,
    private utilService: UtilService,
    private decimalPipe: DecimalPipe,
  ) {

  }

  ngOnInit() {

  }

  verDetalleProtesto(movimiento) {
    const initialState = {
      titulo: `Detalle Protesto (${movimiento.nnumdocto})`,
      cliente: this.clientesService.selectedCliente,
      movimiento: movimiento
    };

    this.bsModalRef = this.modalService.show(DetalleProtestoPopUpComponent, { initialState, backdrop: 'static', keyboard: false });
  }

  verDetalleFactura(movimiento) {

    const initialState = {
      titulo: 'Detalle Factura',
      cliente: this.clientesService.selectedCliente,
      movimiento: movimiento
    };

    this.bsModalRef = this.modalService.show(DetalleFacturaPopUpComponent, { initialState, backdrop: 'static', keyboard: false });
  }

  buscar() {
    this.opcion = 'buscar';
    this.buscaCuentasCorrientesPendiente();
  }

  construirReporte(data: any) {
    const headerExcel = [
      'Fecha Operación',
      'Fecha Emisión',
      'Fecha Vencimiento',
      'Tipo Documento',
      'Nro. Documento',
      'Observación',
      'Cargo',
      'Abono',
      'Saldo',
    ];

    const dataExcel = [];

    data.forEach(e => {
      dataExcel.push([
        this.utilService.dateStringServiceToLocalStringFormat(e.smc_dfhocreacion),
        this.utilService.dateStringServiceToLocalStringFormat(e.smc_nidoperacion),
        e.smc_scodtipooperacion ? this.utilService.dateStringServiceToLocalStringFormat(e.smc_scodtipooperacion) : '',
        e.smc_scodtipodocto,
        e.nnumdocto,
        e.smc_sglosa,
        e.smc_ncargo,
        this.decimalPipe.transform(e.abono, '', 'es'),
        this.decimalPipe.transform(e.smc_nsaldodocto, '', 'es'),
      ]);
    });

    const nombreArchivo = `Cuentas_Pendientes_${this.utilService.dateToStringDateFullFormat(new Date())}00`;
    const titulo = 'Listado Documentos Pendientes';
    const fechas = [`Fecha: ${this.utilService.dateToLocalStringFormat(new Date())}`];

    this.excelService.buildReportCajaCobranza(
      titulo,
      headerExcel,
      dataExcel,
      fechas,
      nombreArchivo,
    );
  }

  exportar() {
    this.opcion = 'exportar';
    this.buscaCuentasCorrientesPendiente();
  }

  buscaCuentasCorrientesPendiente() {
    this.request.nRutCliente = this.clientesService.selectedCliente.cli_nrutcliente;
    this.request.nRutOperador = this.clientesService.selectedCliente.cli_nrutoperador;

    this.cargador.show();
    this.cajaCobranzaService.buscaCuentasCorrientesPendiente(this.request)
      .subscribe((data: any) => {
        this.cargador.hide();

        if (data) {
          if (data.movimientos.length > 0 && data.error.codError === 0) {

            this.cargarMovimientosArray(data.movimientos);
            if (this.opcion === 'exportar') {
              this.construirReporte(this.movimientos);
            }
          } else {
            this.movimientos = [];
            swal('Atención', 'No se encontraron resultados', 'info');
          }

          if (data.error.codError !== 0) {
            swal('Atención', 'Ha ocurrido un error inesperado!', 'error');
          }
        }
      },
        error => {
          this.cargador.hide();
          swal('Atención', 'Ha ocurrido un error inesperado!', 'error');
        });
  }

  cargarMovimientosArray(data) {
    this.movimientos = [];
    data.forEach(e => {

      let observacion = e.smc_sglosa.trim();

      if (e.sinddocumentado === 'S') {
        if (observacion.trim() === '') {
          observacion = 'Factura Documentada';
        } else {
          observacion += ' - Factura Documentada';
        }
      }

      const movimiento = {
        smc_dfhocreacion: e.smc_dfhocreacion,
        smc_nidoperacion: e.smc_nidoperacion,
        smc_scodtipooperacion: e.smc_scodtipooperacion,
        smc_sdestipooperacion: e.smc_sdestipooperacion,
        smc_scodtipodocto: e.smc_scodtipodocto,
        smc_sdestipodocto: e.smc_sdestipodocto,
        smc_niddocto: e.smc_niddocto,
        nnumdocto: e.nnumdocto,
        smc_sglosa: observacion,
        smc_ncargo: e.smc_ncargo,
        smc_nabono: e.smc_nabono,
        smc_nsaldodocto: e.smc_nsaldodocto,
        sinddocumentado: e.sinddocumentado,
        abono: e.smc_ncargo - e.smc_nsaldodocto,
      };
      this.movimientos.push(movimiento);
    });
  }

}
