import { Component, OnInit, OnDestroy } from '@angular/core';
import { FormGroup, FormBuilder, Validators, FormControl } from '@angular/forms';
import { CajaCobranzaService } from '../../../core/consultaService/cajaCobranza.service';
import { AuthService } from '../../../core/consultaService/auth.service';
import { NgxSpinnerService } from 'ngx-spinner';
import { DatePipe } from '@angular/common';
import { UtilService } from './../../../services/util.service';
import { ExcelService } from '../../../core/excel/excel.service';

const swal = require('sweetalert');

@Component({
    selector: 'app-documentos-deposito',
    templateUrl: './documentosDeposito.component.html',
    styleUrls: ['./documentosDeposito.component.scss'],
    providers: [DatePipe]
})
export class DocumentosDepositoComponent implements OnInit, OnDestroy {

    forma: FormGroup;
    user: any = {};
    fechaActual = new Date();

    constructor(
        private cajaCobranzaService: CajaCobranzaService,
        private authService: AuthService,
        public cargador: NgxSpinnerService,
        private utilService: UtilService,
        private excelService: ExcelService,
    ) {
        this.forma = new FormGroup({
            fecha: new FormControl('', Validators.required),
        });
    }

    ngOnInit() {
        this.user = this.authService.getDatosUser();
        this.forma.controls.fecha.setValue(this.utilService.dateToStringFormat(this.fechaActual));
    }

    ngOnDestroy() { }

    buscar() {
        if (this.validarFormulario()) {
            return;
        }
        this.cargador.show();

        const request = {
            nRutOperador: this.user.nrutoperador,
            sFhoDeposito: (this.forma.controls.fecha.value).replace(/-/g, ''),
        };
        this.cajaCobranzaService.buscaDataInfDocRepos(request)
            .subscribe((data: any) => {
                this.cargador.hide();
                if (data) {
                    if (data.error.codError !== 0) {
                        swal('Atención', 'Ha ocurrido un error inesperado!', 'error');
                        return;
                    }
                    if (data.dataInforme.length === 0 && data.error.codError === 0) {
                        // Generar Excel
                        this.construirReporte(data);
                    } else {
                        // Generar Excel
                        this.construirReporte(data);
                    }
                } else {
                    swal('Atención', 'Ha ocurrido un error inesperado!', 'error');
                }
            },
                error => {
                    this.cargador.hide();
                    swal('Atención', 'Ha ocurrido un error inesperado!', 'error');
                });
    }

    validarFormulario() {
        if (this.forma.controls.fecha.value === '') {
            swal('Atención', 'Campo fecha requerido', 'error');
            return true;
        }
        return false;
    }

    convertirFecha(fecha) {
        const regexp = /(\d{4})(\d{2})(\d{2})$/;
        const tag = fecha.match(regexp);
        return new Date(tag[1], tag[2] - 1, tag[3]);
    }

    construirReporte(data: any) {
        const headerExcel = [
            'F. Depósito',
            'Banco',
            'Documento',
            'Monto',
            'Cliente',
        ];

        const dataExcel = [];

        data.dataInforme.forEach(e => {
            dataExcel.push([
                this.utilService.dateStringServiceToLocalStringFormat(e.gar_dfhodeposito),
                e.banco,
                e.gar_niddocto,
                e.gar_nmonto,
                e.gar_nrutcliente + '-' + e.cli_sdigverificador,
            ]);
        });

        const additionalData = [];

        if (data.dataResumenInforme.length > 0) {
            let dataAdicional = [];

            data.dataResumenInforme.forEach(e => {
                dataAdicional.push([
                    e.banco,
                    e.cantdoctos,
                    e.sumamontos,
                ]);
            });
            const item = {
                headerExcel: [
                    'Banco',
                    'Cant Doctos',
                    'Monto',
                ],
                dataExcel: dataAdicional,
            };

            additionalData.push(item);
        }

        const nombreArchivo = `Documento_Deposito_${this.utilService.dateToStringDateFullFormat(new Date())}00`;
        const titulo = 'Informe de Caja Informe de Documentos a Depósito';
        const fechas = [
            `Fecha de Depósito: ${this.utilService.dateToLocalStringFormat(new Date(this.forma.controls.fecha.value.replace(/-/g, '\/')))}`,
        ];

        this.excelService.buildReportCajaCobranza(
            titulo,
            headerExcel,
            dataExcel,
            fechas,
            nombreArchivo,
            null,
            additionalData);
    }

}
