import { Component, OnInit } from '@angular/core';
import { FormGroup, FormBuilder, Validators, FormControl } from '@angular/forms';
import { NgxSpinnerService } from 'ngx-spinner';
import { registerLocaleData } from '@angular/common';
import es from '@angular/common/locales/es';
import { BsModalService, BsModalRef } from 'ngx-bootstrap/modal';
import { DatePipe } from '@angular/common';

const swal = require('sweetalert');

import { ClientesService } from '../../../../../core/consultaService/clientes.service';
import { CajaCobranzaService } from '../../../../../core/consultaService/cajaCobranza.service';
import { DocumentosAsociadosPopUpComponent } from '../../../../commonPopUp/documentosAsociadosPopUp/documentosAsociadosPopUp.component';
import { DetalleFacturaPopUpComponent } from '../../../../commonPopUp/detalleFacturaPopUp/detalleFacturaPopUp.component';
import { ExcelService } from '../../../../../core/excel/excel.service';
import { UtilService } from './../../../../../services/util.service';

@Component({
  selector: 'app-totales',
  templateUrl: './totales.component.html',
  styleUrls: ['./totales.component.scss']
})
export class TotalesComponent implements OnInit {

  forma: FormGroup;
  request: any = {};
  fechaActual = new Date();
  bsModalRef: BsModalRef;

  meses: Array<any> = [
    { codMes: 1, descMes: 'Enero' },
    { codMes: 2, descMes: 'Febrero' },
    { codMes: 3, descMes: 'Marzo' },
    { codMes: 4, descMes: 'Abril' },
    { codMes: 5, descMes: 'Mayo' },
    { codMes: 6, descMes: 'Junio' },
    { codMes: 7, descMes: 'Julio' },
    { codMes: 8, descMes: 'Agosto' },
    { codMes: 9, descMes: 'Septiembre' },
    { codMes: 10, descMes: 'Octubre' },
    { codMes: 11, descMes: 'Noviembre' },
    { codMes: 12, descMes: 'Diciembre' }
  ];

  anios: Array<any> = [];
  movimientos: Array<any> = [];
  fechaOperacionSaldo: Date;
  verData: boolean;
  pageActualTotal = 1;
  saldoAnterior = 0;
  opcion: string;

  constructor(
    private clientesService: ClientesService,
    public cargador: NgxSpinnerService,
    private cajaCobranzaService: CajaCobranzaService,
    private modalService: BsModalService,
    private excelService: ExcelService,
    private utilService: UtilService,
    private datePipe: DatePipe,
  ) {
    this.forma = new FormGroup({
      mes: new FormControl(this.fechaActual.getMonth() + 1),
      anio: new FormControl(this.fechaActual.getFullYear())
    });
  }

  ngOnInit() {
    registerLocaleData(es);
    this.cargaComboAnios();
  }

  cargaComboAnios() {
    const anioActual = this.fechaActual.getFullYear() + 100;
    for (let i = 1900; i <= anioActual; i++) {
      const anio = {
        codAnio: i,
        descAnio: `${i}`
      };
      this.anios.push(anio);
    }
  }

  verDetallePago(movimiento) {
    // this.origen = 'garantia';
    const initialState = {
      titulo: 'Documentos Asociados',
      cliente: this.clientesService.selectedCliente,
      movimiento: movimiento
    };

    this.bsModalRef = this.modalService.show(DocumentosAsociadosPopUpComponent, { initialState, backdrop: 'static', keyboard: false });
  }

  verDetalleFactura(movimiento) {
    // this.origen = 'garantia';
    const initialState = {
      titulo: 'Detalle Factura',
      cliente: this.clientesService.selectedCliente,
      movimiento: {
        cte_niddocto: movimiento.cte_nidoperacion,
        nnumdocto: movimiento.nnumdocto,
      }
    };

    this.bsModalRef = this.modalService.show(DetalleFacturaPopUpComponent, { initialState, backdrop: 'static', keyboard: false });
  }

  validarForm() {
    if (this.forma.controls.anio.value > this.fechaActual.getFullYear()) {
      swal('Atención', 'El período seleccionado debe ser menor o igual al período actual', 'info');
      return true;
    }

    if (this.forma.controls.mes.value > this.fechaActual.getMonth() + 1 &&
      this.forma.controls.anio.value == this.fechaActual.getFullYear()) {
      swal('Atención', 'El período seleccionado debe ser menor o igual al período actual', 'info');
      return true;
    }

    return false;
  }


  buscaCuentasCorrientes() {

    if (this.validarForm()) {
      return;
    }

    this.verData = false;
    this.request.nAno = this.forma.controls.anio.value;
    this.request.nMes = this.forma.controls.mes.value;
    this.request.nRutCliente = this.clientesService.selectedCliente.cli_nrutcliente;
    this.request.nRutOperador = this.clientesService.selectedCliente.cli_nrutoperador;

    this.cargador.show();
    this.cajaCobranzaService.buscaCuentasCorrientes(this.request)
      .subscribe((data: any) => {
        this.cargador.hide();

        if (data) {
          this.saldoAnterior = data.nSaldoAnterior;

          this.verData = true;
          if (data.movimientos.length > 0 && data.error.codError === 0) {
            this.cargarMovimientosArray(data.movimientos, data.nSaldoAnterior);
            if (this.opcion === 'exportar') {
              this.construirReporte(this.movimientos);
            }
          } else {
            this.movimientos = [];
            swal('Atención', 'No se encontraron resultados', 'info');
          }

          if (data.error.codError != 0) {
            swal('Atención', 'Ha ocurrido un error inesperado!', 'error');
          }
        }
      },
        error => {
          this.cargador.hide();
          swal('Atención', 'Ha ocurrido un error inesperado!', 'error');
        });

  }

  buscar() {
    this.opcion = 'buscar';
    this.buscaCuentasCorrientes();
  }

  cargarMovimientosArray(data, saldoAnterior) {
    this.fechaOperacionSaldo = this.lastday(this.forma.controls.anio.value, this.forma.controls.mes.value - 1);

    this.movimientos = [];
    let saldo = saldoAnterior;
    data.forEach(e => {
      saldo = saldo + (e.cte_ncargo - e.cte_nabono);
      const movimiento = {
        cte_dfhocreacion: e.cte_dfhocreacion,
        cte_nidoperacion: e.cte_nidoperacion,
        cte_scodtipooperacion: e.cte_scodtipooperacion,
        cte_scodtipodocto: e.cte_scodtipodocto,
        cte_sdestipodocto: e.cte_sdestipodocto,
        nnumdocto: e.nnumdocto,
        cte_sglosa: e.cte_sglosa,
        cte_ncargo: e.cte_ncargo,
        cte_nabono: e.cte_nabono,
        sinddocasociados: e.sinddocasociados,
        nSaldoAnterior: saldo,
      };

      this.movimientos.push(movimiento);
    });
  }


  construirReporte(data: any) {
    const headerExcel = [
      'Fecha Operación',
      'Nº Operación',
      'Tipo Operación',
      'Tipo Documento',
      'Nro. Documento',
      'Observación',
      'Cargo',
      'Abono',
      'Saldo',
    ];

    const dataExcel = [];
    const saldoFecha = this.lastday(this.forma.controls.anio.value, this.forma.controls.mes.value - 1);

    dataExcel.push([
      this.utilService.dateToLocalStringFormat(saldoFecha),
      '',
      '',
      '',
      '',
      '',
      this.saldoAnterior > 0 ? this.saldoAnterior : 0,
      this.saldoAnterior < 0 ? (this.saldoAnterior * -1) : 0,
      this.saldoAnterior,
    ]);

    data.forEach(e => {
      dataExcel.push([
        this.utilService.dateStringServiceToLocalStringFormat(e.cte_dfhocreacion),
        e.cte_nidoperacion,
        e.cte_scodtipooperacion,
        e.cte_scodtipodocto,
        e.nnumdocto,
        e.cte_sglosa,
        e.cte_ncargo,
        e.cte_nabono,
        e.nSaldoAnterior,
      ]);
    });

    const nombreArchivo = `Cuentas_${this.utilService.dateToStringDateFullFormat(new Date())}00`;
    const titulo = null;
    const fechas = [
      `Fecha: ${this.utilService.dateToLocalStringFormat(new Date())}`,
    ];

    this.excelService.buildReportCajaCobranza(
      titulo,
      headerExcel,
      dataExcel,
      fechas,
      nombreArchivo
    );

  }

  exportar() {
    this.opcion = 'exportar';
    this.buscaCuentasCorrientes();
  }

  lastday(y, m) {
    return new Date(y, m, 0);
  }

}
